import React from 'react'
import { useContext, useState } from 'react';
import { DishContext } from '../../App'
import './styles.css'

function Dish(props) {

    const dishContext = useContext(DishContext);
    const [active, setActive] = useState(false);

    const handleClick = (e, dishContext, name, price, link, id) => {
        
        setActive(true) 

        return dishContext(name, price, link, id)
    }

    return (
        <div className="dish">

            <div className="img-box">
                <div className={`background-image ${active ? `activeImg` : ''}`} 
                     style={{backgroundImage:`url(${props.link})`}}
                     onClick={ (e) => handleClick(e, dishContext, props.name, props.price, props.link, props.id) } />
                <img src="/Gourmet-World/icons/shopping-cart.svg" alt="Add to cart" className={`img-cart ${active ? `activeCart` : ''}`} />
            </div>

            <div className="dish__description">
                <h2 className="dish__title">{ props.name }</h2>
                <span className="dish__price">{ props.price } </span>
                <div className="dish__text">{ props.text }</div>
            </div>         
            
        </div>
    )
}

export default Dish